.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

body {
  font-family: 'Work Sans', sans-serif;
  background: url(../img/under_construction.jpg) no-repeat center;
  background-size: cover;
  background-attachment: fixed;
}

p {
  font-size: 16px;
  font-size: 1rem;
  line-height: 24px;
  line-height: 1.5rem;
}

.text-center {
  text-align: center;
}

/* Header */
a {
  text-decoration: none;
}

.storms-container {
  max-width: 1024px;
  padding: 5vw 5vw 0;
  margin: 0 auto;
}

.storms-content {
  background-color: #bbbbbb70;
  padding: 25px;
}

.message h1 {
  padding-top: 1.5em;
  color: #fff;
  display: inline-block;
  font-size: 44.8px;
  font-size: 2.8rem;
  font-family: 'Work Sans', sans-serif;
  text-transform: capitalize;
  font-weight: 600;
  letter-spacing: 1px;
}

.left-grid {
  border-right: 1px solid #fff;
}

.left-grid h2.logo img {
  max-width: 100%;
  padding-right: 20px;
}

.right-grid {
  padding-left: 50px;
  /*-- social-icons --*/
}

.right-grid p {
  color: #fff;
  margin: 12px 0;
  text-align: left;
}

.right-grid p:first-child {
  margin-bottom: 22px;
}

.right-grid .info-grids p {
  color: #fff;
  margin: 12px 0;
  text-align: left;
  letter-spacing: .7px;
}

.right-grid .info-grids p span.fa {
  padding-right: 10px;
  color: #373a40;
}

.right-grid .info-grids a {
  padding: 0;
  color: #000000;
}

.right-grid .info-grids a:hover {
  color: #bec0c1;
}

.right-grid .social-grids {
  margin: 1em 0;
  flex-basis: 100%;
  text-align: left;
}

.right-grid .social-grids ul {
  padding: 0;
  margin: 0;
}

.right-grid .social-grids ul li {
  display: inline-block;
}

.right-grid .social-grids ul li:not(:last-child) span.fa {
  margin: 0 1em 0 0;
}

.right-grid .social-grids ul li a {
  color: #ffffff;
  text-align: center;
  font-size: 32px;
  font-size: 2rem;
}

.right-grid .social-grids ul li a span.fa.fa-facebook:hover {
  color: #3b5998;
}

.right-grid .social-grids ul li a span.fa.fa-twitter:hover {
  color: #55acee;
}

.right-grid .social-grids ul li a span.fa.fa-instagram:hover {
  color: #f26522;
}

.right-grid .social-grids ul li a span.fa.fa-whatsapp:hover {
  color: #078e31;
}

.right-grid .social-grids ul li a span.fa.fa-youtube:hover {
  color: #d20c03;
}

.right-grid .social-grids ul li a span.fa.fa-linkedin:hover {
  color: #1b3c8e;
}

.footer p {
  text-align: center;
  color: #fff;
  letter-spacing: 1px;
  font-weight: 400;
  padding: 0 2em 2em;
}

.footer p a {
  color: #fff;
}

.footer p a:hover {
  color: #84C3E3;
}

@media screen and (max-width: 1080px) {
  .storms-container {
    width: 95%;
  }
}

@media screen and (max-width: 900px) {
  .storms-container {
    width: 98%;
  }
}

@media screen and (min-width: 801px) {
  .storms-content {
    display: flex;
    margin-bottom: 5em;
  }
  .left-grid,
  .right-grid {
    margin: auto 0;
    -webkit-box-flex: 1;
    /* OLD - iOS 6-, Safari 3.1-6 */
    -moz-box-flex: 1;
    /* OLD - Firefox 19- */
    width: 20%;
    /* For old syntax, otherwise collapses. */
    -webkit-flex: 1;
    /* Chrome */
    -ms-flex: 1;
    /* IE 10 */
    flex: 1;
    /* NEW, Spec - Opera 12.1, Firefox 20+ */
  }
}

@media screen and (max-width: 800px) {
  .storms-container {
    width: 72%;
    margin: 0 auto 3em;
  }
  .storms-container {
    padding: 4vw 5vw 0;
  }
  .left-grid {
    border-bottom: 1px solid #fff;
    border-right: none;
    padding-bottom: 2em;
  }
  .right-grid {
    padding-left: 0;
    padding-top: 20px;
  }
}

@media screen and (max-width: 667px) {
  .storms-container {
    width: 78%;
  }
  .message h1 {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 600px) {
  p {
    font-size: .8rem;
  }
}

@media screen and (max-width: 480px) {
  .storms-container {
    width: 88%;
  }
  .storms-content {
    padding: 15px;
  }
  .message h1 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 414px) {
  .storms-container {
    width: 88%;
  }
}

@media screen and (max-width: 384px) {
  .message h1 {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 320px) {
  .message h1 {
    font-size: 1.5rem;
  }
}
